<template>
    <mt-popup class="add-under-popup" popup-transition="popup-fade" :modal=true v-model="isShow">
        <div class="popup-close">
            <i class="iconfont icon-close" @click="isShow=false"></i>
        </div>
        <div class="add-man-info">
            <div class="img-container">
                <img v-lazy="cellData.headimgurl" class="img" />
            </div>
            <p class="man-name">{{cellData.username}}</p>
        </div>
        <div class="btns">
            <mt-button size="small" class="btn cancel" type="default" @click="cancelBtnClickHandle">取消</mt-button>
            <mt-button size="small" class="btn sure" type="default" @click="sureBtnClickHandle">确定赠送</mt-button>
        </div>
    </mt-popup>
</template>

<script>
import { mapState } from "vuex";
import Vue from "vue";
import { Popup, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/popup/style.css";
Vue.component(Button.name, Button);
Vue.component(Popup.name, Popup);

export default {
  name: "give-meal-popup",
  data() {
    return {
      isShow: false,
      cellData: {}
    };
  },
  components: {},
  created() {},
  methods: {
    sureBtnClickHandle() {
      this.$emit("request-give-meal");
      this.isShow = false;
    },
    cancelBtnClickHandle(){
      this.isShow = false;
    }
  },
  computed: {}
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.add-under-popup {
    width: 84%;
    border-radius: 8px;
    text-align: center;
    padding-bottom: 18px;

    .popup-close {
        text-align: right;

        .iconfont {
            color: #CBCBCB;
            font-size: 24px;
            line-height: 24px;
            font-weight: 700;
            margin-top: 8px;
            margin-right: 8px;
            display: inline-block;
        }
    }

    .add-man-info {
        width: 100%;
        margin-top: 8px;

        .img-container {
            display: inline-block;
            width: 70px;
            height: 70px;

            .img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                position: relative;
            }
        }

        .man-name {
            color: #42495C;
            font-size: 16px;
            font-weight: 700;
            margin-top: 5px;
        }
    }

    .btns {
        width: 100%;
        margin-top: 20px;
        display: flex;
        align-items: center;
        text-align: center;
        height: 40px;

        .btn {
            flex: 1;
            height: 38px;
            color: #fff;
            background-color: #565d71;
            font-size: 15px;

            &.cancel {
                letter-spacing: 4px;
                margin: 0px 7px 0px 20px;
            }

            &.sure{
                margin: 0px 20px 0px 7px;
            }
        }
    }
}
</style>



