<template>
  <div class="store-buyed">
    <div @touchmove.prevent class="chain-tab">
      <div class="chain-tab-ul">
        <div class="chain-tab-li">
          <router-link :to="'storeMealList'" replace>
            <div class="chain-tab-li-hot">
              <span class="chain-name">套餐商品</span>
            </div>
          </router-link>
        </div>
        <div class="chain-tab-li">
          <router-link :to="'smartGoodsList'" replace>
            <div class="chain-tab-li-hot">
              <span class="chain-name">店铺商城商品</span>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <give-meal-popup @request-give-meal="requestGiveMeal" ref="givePopup"></give-meal-popup>
    <keep-alive>
        <router-view class="chain-tab-view"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import giveMealPopup from "../../popupBox/giveMealPopup.vue";
import { mapState } from "vuex";
import Vue from "vue";
import { Toast } from "mint-ui";
import { MessageBox } from "../../popupBox/messageBox/message-box.js";
import "mint-ui/lib/toast/style.css";
Vue.component(MessageBox.name, MessageBox);
Vue.component(Toast.name, Toast);

export default {
  name: "store-buyed",
  data() {
    return {
      gavingData: null,
      personData: null
    };
  },
  components: {
    giveMealPopup
  },
  created() {},
  beforeRouteLeave(to, from, next) {
    MessageBox.close();
    let givePopup = this.$refs.givePopup;
    if (givePopup && givePopup.isShow) {
      givePopup.isShow = false;
    }
    next();
  },
  computed: {
    ...mapState(["userid", "path", "role"])
  },
  methods: {
    giveBtnClickHandle(goods) {
      this.gavingData = goods;
      // document.body.style.overflow='hidden';
      MessageBox.prompt("", { message: "请输入对方的手机号码", title: "" })
        .then(({ value, action }) => {
          if (action != "confirm") return;
          if (!value || value.length < 11) {
            Toast({
              message: "请输入有效的手机号码",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
            return;
          }
          var phnum = value.slice(0, 11);
          if (!/^1[34578]\d{9}$/.test(phnum)) {
            Toast({
              message: "请输入有效的手机号码",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
            return;
          }
          this.searchGivePersonHandle(value);
        })
        .catch(response => {
          console.log("取消了");
        });
    },
    searchGivePersonHandle(phoneNum) {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php", {
          type: "finduserbyphone",
          phone: phoneNum,
          role: 0
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          if (response.body.error != "ok") {
            Toast({
              message: "该手机号码还未绑定",
              iconClass: "iconfont icon-tishi1",
              duration: 2000
            });
            return;
          }
          let resultData = response.body.arr;
          if (resultData.length == 0) {
            Toast({
              message: "该手机号码还未绑定",
              iconClass: "iconfont icon-tishi1",
              duration: 2000
            });
            return;
          }
          let personData = resultData[0];
          if (personData.role.toString() != "1") {
            Toast({
              message: "该用户不是普通用户",
              iconClass: "iconfont icon-tishi1",
              duration: 2000
            });
            return;
          }
          this.personData = personData;
          this.showgivePopup();
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
          Toast({
            message: "该手机号码还未绑定",
            iconClass: "iconfont icon-tishi1",
            duration: 2000
          });
          this.$store.dispatch("toggleLoading", false);
        });
    },
    requestGiveMeal() {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php", {
          type: "goodsgive",
          guid: this.userid,
          tguid: this.personData.userid,
          buyid: this.gavingData.buyid
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          if (response.body.error != "ok") {
            if (response.body.error == "-103")
              Toast({
                message: "该用户已经绑定了其他商家",
                iconClass: "iconfont icon-tishi1",
                duration: 2000
              });
            else
              Toast({
                message: "赠送失败!",
                iconClass: "iconfont icon-tishi1",
                duration: 2000
              });
            return;
          }
          this.gavingData.hadGive = true;
          this.gavingData.giveNum = this.personData.phone;
          Toast({
            message: "赠送成功!",
            iconClass: "iconfont icon-tishi1",
            duration: 2000
          });
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
          Toast({
            message: "赠送失败!",
            iconClass: "iconfont icon-tishi1",
            duration: 2000
          });
          this.$store.dispatch("toggleLoading", false);
        });
    },
    showgivePopup() {
      let givePopup = this.$refs.givePopup;
      givePopup.cellData = this.personData;
      givePopup.isShow = true;
    },
    hidegivePopup() {
      this.$refs.givePopup.isShow = false;
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.store-buyed {
  width: 100%;
  padding-top: 0.46rem;

  .chain-tab-view {
    overflow: hidden;
  }

  .chain-tab {
    top: 0;
    position: fixed;
    height: 0.36rem;
    background-color: #fff;
    width: 100%;
    z-index: 10;

    .chain-tab-ul {
      margin: 0px auto;
      height: 100%;
      width: 80%;
      display: flex;
      align-items: center;
      text-align: center;

      .chain-tab-li {
        overflow: hidden;
        height: 100%;
        flex: 1;

        .chain-tab-li-hot {
          width: 100%;
          height: 100%;
          position: relative;

          .chain-name {
            line-height: 0.36rem;
            color: #42495C;
            font-size: 0.12rem;
            position: relative;
            top: 0.01rem;
            letter-spacing: 0.01rem;
          }
        }

        .chain-tab-li-hot::after {
          position: absolute;
          bottom: -3px;
          left: 15%;
          width: 70%;
          height: 2px;
          background: #FD8709;
          border-radius: 1px;
          content: '';
          -webkit-transition: -webkit-transform 0.2s;
          transition: transform 0.2s;
          -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
        }

        .active {
          .chain-name {
            top: 0px;
            color: #FD8709;
          }

          .chain-tab-li-hot::after {
            -webkit-transform: translate3d(0, -150%, 0);
            transform: translate3d(0, -150%, 0);
          }
        }
      }
    }
  }
}

.mint-msgbox {
  width: 80%;

  .mint-msgbox-content {
    min-height: 0px;
    text-align: center;

    .mint-msgbox-message {
      line-height: 20px;
      margin-top: 10px;
    }

    .mint-msgbox-input {
      width: 85%;
      display: inline-block;
      padding-bottom: 6px;

      input {
        position: relative;
        left: -5px;
      }

      .mint-msgbox-errormsg {
        display: none;
      }
    }
  }

  .mint-msgbox-btns {
    .mint-msgbox-btn {
      font-size: 16px;
    }
  }
}
</style>



